"use client";

import React, { useState } from "react";
import { useRouter } from "next/navigation";
import { Card, CardContent, CardFooter, CardHeader } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Avatar, AvatarFallback, AvatarImage } from "@/components/ui/avatar";

export interface Post {
  id: string;
  username: string;
  userAvatar?: string;
  imageUrl: string;
  caption: string;
  likes: number;
  comments: Comment[];
  timestamp: string;
  isLiked?: boolean;
}

export interface Comment {
  id: string;
  username: string;
  text: string;
  timestamp: string;
}

interface PostCardProps {
  post: Post;
  onLike?: (postId: string) => void;
  onComment?: (postId: string, comment: string) => void;
  onViewPost?: (postId: string) => void;
}

export const PostCard = ({ post, onLike, onComment, onViewPost }: PostCardProps) => {
  const router = useRouter();
  const [imgError, setImgError] = useState(false);
  const [isLiked, setIsLiked] = useState(post.isLiked || false);
  const [likeCount, setLikeCount] = useState(post.likes);

  const handleLike = () => {
    setIsLiked(!isLiked);
    setLikeCount(isLiked ? likeCount - 1 : likeCount + 1);
    onLike?.(post.id);
  };

  const handleViewPost = () => {
    if (onViewPost) {
      onViewPost(post.id);
    } else {
      router.push(`/post/${post.id}`);
    }
  };

  return (
    <Card className="w-full max-w-lg mx-auto">
      <CardHeader className="pb-3">
        <div className="flex items-center space-x-3">
          <Avatar className="h-8 w-8">
            <AvatarImage src={post.userAvatar} alt={post.username} />
            <AvatarFallback>{post.username.charAt(0).toUpperCase()}</AvatarFallback>
          </Avatar>
          <div className="flex-1">
            <p className="text-sm font-semibold">{post.username}</p>
            <p className="text-xs text-muted-foreground">{post.timestamp}</p>
          </div>
        </div>
      </CardHeader>

      <CardContent className="p-0">
        {!imgError ? (
          <img
            src={post.imageUrl}
            alt={post.caption}
            className="w-full aspect-square object-cover"
            onError={() => setImgError(true)}
          />
        ) : (
          <div className="w-full aspect-square flex items-center justify-center bg-muted text-muted-foreground">
            <div className="text-center">
              <p className="text-sm">Image unavailable</p>
              <p className="text-xs">Failed to load image</p>
            </div>
          </div>
        )}
      </CardContent>

      <CardFooter className="flex-col items-start space-y-3 pt-3">
        <div className="flex items-center justify-between w-full">
          <div className="flex items-center space-x-4">
            <Button
              variant="ghost"
              size="sm"
              onClick={handleLike}
              className={`p-0 h-auto font-semibold ${
                isLiked ? "text-red-500" : "text-foreground"
              }`}
            >
              {isLiked ? "♥" : "♡"} {likeCount}
            </Button>
            <Button
              variant="ghost"
              size="sm"
              onClick={handleViewPost}
              className="p-0 h-auto font-semibold text-foreground"
            >
              💬 {post.comments.length}
            </Button>
          </div>
          <Button
            variant="ghost"
            size="sm"
            onClick={handleViewPost}
            className="p-0 h-auto text-muted-foreground"
          >
            View Post
          </Button>
        </div>

        {post.caption && (
          <div className="w-full">
            <p className="text-sm">
              <span className="font-semibold">{post.username}</span>{" "}
              {post.caption}
            </p>
          </div>
        )}

        {post.comments.length > 0 && (
          <div className="w-full">
            <Button
              variant="ghost"
              size="sm"
              onClick={handleViewPost}
              className="p-0 h-auto text-muted-foreground"
            >
              View all {post.comments.length} comments
            </Button>
            <div className="mt-1">
              <p className="text-sm">
                <span className="font-semibold">{post.comments[0].username}</span>{" "}
                {post.comments[0].text}
              </p>
            </div>
          </div>
        )}
      </CardFooter>
    </Card>
  );
};
