"use client";

import { useState } from "react";
import { PostCard } from "@/components/PostCard";
import { mockPosts } from "@/lib/mockData";

export default function FeedPage() {
  const [posts, setPosts] = useState(mockPosts);

  const handleLike = (postId: string) => {
    setPosts(prevPosts =>
      prevPosts.map(post =>
        post.id === postId
          ? {
              ...post,
              isLiked: !post.isLiked,
              likes: post.isLiked ? post.likes - 1 : post.likes + 1
            }
          : post
      )
    );
  };

  const handleViewPost = (postId: string) => {
    // In a real app, this would navigate to a detailed post view
    console.log("Viewing post:", postId);
    // For now, we'll just show an alert
    alert(`Opening detailed view for post ${postId}`);
  };

  return (
    <div className="space-y-8">
      <div className="text-center space-y-2">
        <h1 className="text-3xl font-bold tracking-tight">Your Feed</h1>
        <p className="text-muted-foreground">
          Discover and engage with posts from the community
        </p>
      </div>

      <div className="space-y-8">
        {posts.map((post) => (
          <PostCard
            key={post.id}
            post={post}
            onLike={handleLike}
            onViewPost={handleViewPost}
          />
        ))}
      </div>

      {posts.length === 0 && (
        <div className="text-center py-16">
          <h3 className="text-lg font-semibold mb-2">No posts yet</h3>
          <p className="text-muted-foreground">
            Be the first to share something amazing!
          </p>
        </div>
      )}
    </div>
  );
}
